.form {
	justify-content: center;
	display: inherit;
	white-space: nowrap;
	left: 540px;
	height: 40px;
	width: 40px;
}

.form2 {
	position: relative;
	bottom: 180px;
}
.output {
	background-color: white;
	position: relative;
	bottom: 40px;
	color: #3e6d9c;

	font-weight: 700;
	font-size: 25px;
	font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande", "Lucida Sans Unicode", Geneva, Verdana,
		sans-serif;
}
.input {
	border: 2px solid;
	border-radius: 5px;
	display: inline-block;
	margin-right: 10px;
	height: 40px;
}
.submit {
	border: 2px solid;
	border-radius: 5px;
	border: 2px solid;
}
.select {
	height: 39px;
	border: 2px solid;
	border-radius: 5px;
	border: 2px solid;
	margin-right: 10px;
	padding: 1px 15px;
}
.Category {
	margin-right: 10px;
	font-weight: 800;
	font-size: 1.3rem;
	color: #3e6d9c;
}
.contratax {
	display: flex;
	width: 1200px;
	height: 600px;
	z-index: -1;
}
